/* Experience Section */
.experience-section {
  padding: 5rem 0;
  background: var(--background-color);
}

.section-title {
  text-align: center;
  font-size: clamp(2rem, 5vw, 2.5rem);
  margin-bottom: 3rem;
  color: var(--heading-color);
}

.experience-container {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 2rem;
  background: var(--card-bg);
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

.experience-tabs {
  background: var(--background-color);
  padding: 1rem 0;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.experience-tab {
  width: 100%;
  padding: 1rem;
  background: transparent;
  border: none;
  cursor: pointer;
  text-align: left;
  transition: var(--transition);
  border-left: 3px solid transparent;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.experience-tab:hover {
  background: rgba(0, 0, 0, 0.05);
}

.experience-tab.active {
  background: var(--card-bg);
  border-left-color: var(--primary-color);
}

.experience-tab .company {
  font-weight: 600;
  color: var(--heading-color);
}

.experience-tab .duration {
  font-size: 0.875rem;
  color: var(--text-color);
  opacity: 0.8;
}

.experience-content {
  padding: 2rem;
}

.experience-header {
  margin-bottom: 2rem;
}

.experience-header h3 {
  font-size: 1.5rem;
  color: var(--heading-color);
  margin-bottom: 0.5rem;
}

.company-name {
  font-size: 1.1rem;
  color: var(--primary-color);
  font-weight: 500;
}

.responsibilities {
  margin-bottom: 2rem;
}

.responsibilities h4,
.technologies h4 {
  font-size: 1.1rem;
  color: var(--heading-color);
  margin-bottom: 1rem;
}

.responsibilities ul {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 1rem;
}

.responsibilities ul li {
  position: relative;
  padding-left: 1.5rem;
  line-height: 1.6;
}

.responsibilities ul li::before {
  content: "▹";
  position: absolute;
  left: 0;
  color: var(--primary-color);
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: var(--background-color);
  color: var(--primary-color);
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  font-size: 0.875rem;
  font-weight: 500;
  transition: var(--transition);
}

.tech-tag:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .experience-container {
    grid-template-columns: 1fr;
  }

  .experience-tabs {
    display: flex;
    overflow-x: auto;
    border-right: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0;
  }

  .experience-tab {
    min-width: 200px;
    border-left: none;
    border-bottom: 3px solid transparent;
    padding: 1rem;
  }

  .experience-tab.active {
    border-left-color: transparent;
    border-bottom-color: var(--primary-color);
  }

  .experience-content {
    padding: 1.5rem;
  }
}

/* About Section */
.about-section {
  padding: 5rem 0;
  background: var(--background-color);
}

.about-container {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 3rem;
  align-items: start;
}

.about-image {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.about-image img {
  width: 100%;
  height: auto;
  display: block;
  transition: var(--transition);
}

.about-image:hover img {
  transform: scale(1.05);
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(37, 99, 235, 0.1), rgba(59, 130, 246, 0.1));
}

.about-content {
  padding: 1rem;
}

.about-text h3 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.intro {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 2rem;
  color: var(--text-color);
}

.highlights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin: 2rem 0;
}

.highlight-card {
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: var(--transition);
}

.highlight-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.highlight-card h4 {
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.expertise-areas {
  margin: 2rem 0;
}

.expertise-areas h4 {
  margin-bottom: 1rem;
  color: var(--heading-color);
}

.expertise-areas ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  list-style: none;
  padding: 0;
}

.expertise-areas li {
  position: relative;
  padding-left: 1.5rem;
  line-height: 1.6;
}

.expertise-areas li::before {
  content: "▹";
  position: absolute;
  left: 0;
  color: var(--primary-color);
}

.personal-note {
  font-style: italic;
  color: var(--text-color);
  line-height: 1.8;
  margin-top: 2rem;
}

/* Skills Section */
.skills-section {
  padding: 5rem 0;
  background: var(--card-bg);
}

.skills-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.skill-category {
  background: var(--background-color);
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.skill-category h3 {
  color: var(--heading-color);
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
}

.skills-list {
  display: grid;
  gap: 1rem;
}

.skill-item {
  background: var(--card-bg);
  padding: 1rem;
  border-radius: 0.5rem;
}

.skill-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.skill-name {
  font-weight: 500;
  color: var(--heading-color);
}

.skill-percentage {
  color: var(--primary-color);
  font-weight: 600;
}

.skill-bar {
  height: 6px;
  background: var(--background-color);
  border-radius: 3px;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
  border-radius: 3px;
  transition: width 1s ease-in-out;
}

/* Education Section */
.education-section {
  padding: 5rem 0;
  background: var(--background-color);
}

.education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.education-card {
  background: var(--card-bg);
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: var(--transition);
  display: flex;
  flex-direction: column;
}

.education-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.education-year {
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  font-weight: 500;
  text-align: center;
}

.education-content {
  padding: 1.5rem;
}

.education-content h3 {
  color: var(--heading-color);
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
}

.institution {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-bottom: 1rem;
}

.institution-name {
  color: var(--primary-color);
  font-weight: 500;
}

.location {
  font-size: 0.875rem;
  color: var(--text-color);
  opacity: 0.8;
}

.description {
  color: var(--text-color);
  line-height: 1.6;
}

@media (max-width: 768px) {
  .about-container {
    grid-template-columns: 1fr;
  }

  .about-image {
    max-width: 300px;
    margin: 0 auto;
  }

  .highlights-grid {
    grid-template-columns: 1fr;
  }

  .expertise-areas ul {
    grid-template-columns: 1fr;
  }

  .skills-container {
    grid-template-columns: 1fr;
  }

  .education-grid {
    grid-template-columns: 1fr;
  }
}

.success-message {
  background-color: #d4edda;
  color: #155724;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
  border: 1px solid #c3e6cb;
}

.error-message {
  background-color: #f8d7da;
  color: #721c24;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
  border: 1px solid #f5c6cb;
}
